  background: linear-gradient(135deg, var(--accent-color), var(--accent-dark));
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--accent-text);
  font-weight: 600;
  font-size: 14px;
  flex-shrink: 0;
}

.member-avatar-small .avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

